.boxs {
  padding: 30px 40px;
  box-sizing: border-box;
  margin-bottom: 20px;
  border-radius: 4px;
  color: #283e53;
}
.boxs:last-child {
  margin-bottom: 0;
}
.boxs .small-tit {
  margin-bottom: 20px;
  color: #6c849c;
  font-size: 16px;
}
.boxs .info-list li {
  margin: 10px 0;
}
.boxs .info-list li .lbs {
  width: 100px;
  text-align: right;
  margin-right: 20px;
}
.boxs .problem-list li {
  margin-bottom: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #d4e1e9;
  box-sizing: border-box;
}
.boxs .problem-list li .lis-head {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  margin-right: 10px;
}
.boxs .problem-list li .lis-content {
  width: calc(100% - 55px);
}
.boxs .problem-list li .lis-content .name {
  color: #6c849c;
}
.boxs .problem-list li .lis-content .content-txt {
  margin: 10px 0;
}
.boxs .problem-list li .lis-content .operation-box {
  margin-top: 10px;
}
.boxs .problem-list li .lis-content .operation-box .time {
  color: #9eabbc;
  font-size: 12px;
}
.boxs .problem-list li .lis-content .operation-box .right-box a {
  margin-right: 20px;
}
.boxs .problem-list li .lis-content .operation-box .right-box a:last-child {
  margin-right: 0;
}
.boxs .problem-list li .lis-content .reply-box {
  margin-top: 10px;
}
.boxs .problem-list li .lis-content .reply-box .inputs {
  border: 1px solid #d4e1e9;
  border-radius: 2px;
  padding: 0 15px;
  height: 36px;
  box-sizing: border-box;
  width: 100%;
}
.boxs .problem-list li .lis-content .reply-box .btn-box {
  margin-top: 15px;
  text-align: right;
}
.boxs .problem-list li .lis-content .reply-box .btn-box .btns {
  margin-right: 20px;
  padding: 0 15px;
}
.boxs .problem-list li .lis-content .reply-box .btn-box .btns:last-child {
  margin-right: 0;
}
.boxs .problem-list li .lis-content .forward-box {
  margin-top: 20px;
  background: #eef1f7;
  padding: 15px 5px;
  box-sizing: border-box;
}
.boxs .answer-list {
  border: 1px solid #d4e1e9;
  padding: 30px 40px;
  box-sizing: border-box;
}
.boxs .answer-list li {
  margin-bottom: 30px;
}
.boxs .answer-list li:last-child {
  margin-bottom: 0;
}
.boxs .answer-list .lbs {
  display: block;
}
.boxs .answer-list .uninps {
  width: 100%;
}
.boxs.answer-box .btn-box {
  margin-top: 20px;
  text-align: right;
}
